import React,{useState,useEffect } from 'react';
import { toast } from 'react-toastify';
import './AddEdit.css';
//import {getDatabase , ref ,set}  from "firebase/database";
// import 'firebase/compat/database';
import fireDb from '../firebase'
import { Link, useNavigate, useParams } from 'react-router-dom';


const initialState = {
    emp:"",
    name:"",
    email:"",
    contact:"",
    maritalstatus:"",
    gcmlevel:"",
    office1:"N/A",
    office2:"N/A",
    office3:"N/A",
    dateofjoining:"",
    salary:"",
    benchstatus: false,
};

const AddEdit = () => {

 const [state, setState] = useState(initialState);
 const [data, setData] = useState({});

 const {emp,name, email, contact,dateofjoining,salary,benchstatus} = state;
 
 const history = useNavigate();
 const {id} = useParams();
 
 useEffect(() => {
    fireDb.child("conacts").on("value" , (snapshot)=>{
      if(snapshot.val() !== null) {
        setData( { ...snapshot.val() });
      }else{
        setData({});
      }
    })
    
    return () => {
      setData({});
    }
  }, [id]);

  useEffect(() => {
    if(id){
        setState({ ...data[id]})
    }else{
        setState({ ...initialState})
    }
    return()=>{
        setState({ ...initialState})
    }
  }, [id ,data])
  
  useEffect(() => {
   console.log(state)
  }, [])

  useEffect(() => {
    console.log(state)
   }, [state])





 const handleInputChange = (e) => {
    const {name,value} = e.target;
    setState({ ...state, [name]: value  });
    console.log(name,value)
 };

 const handleSubmit = (e) => 
 {
    e.preventDefault();
    if(!emp ||!name || !email || !contact)
    {
        toast.error("please provide value in each input field");
    }
    else
    {
        if(!id){
         fireDb.child("conacts").push(state, (err) =>{
            if(err){
                toast.error("System error"+err);
            }else{
                toast.success("user added successfully")
            }
         });
        } else{
            fireDb.child(`conacts/${id}`).set(state, (err) =>{
                if(err){
                    toast.error("System error"+err);
                }else{
                    toast.success("user updated successfully")
                }
            });
        }


        setTimeout( ()=> history("/"), 500);





    //     try {
    //     const fireDb = getDatabase();
    //     set(ref(fireDb, 'conacts/'+id), state );      
    //     toast.success("Contact Added Successfully");
    //     setTimeout( ()=> history("/"), 500);
    // } catch (error) {
    //         toast.error("System error =>081-048-9");
    //     }

    }
    //setTimeout( ()=> history("/"), 500);
    
 };

 return (
        <div style={{marginTop: "100px"}}>
            <form 
            style={{margin: "auto" , padding: "15px" , maxWidth: "400px" , alignContent:"center"}}
            onSubmit={handleSubmit}            
            >

                <label htmlFor="id"><b>Emp ID</b></label>
                <input
                 type="number" 
                 id="emp" 
                 name="emp" 
                 placeholder="Emp Id..." 
                 value={emp || ""} 
                 onChange={handleInputChange}
                 />

                <label htmlFor="name"><b>Name </b></label>
                <input
                 type="text" 
                 id="name" 
                 name="name" 
                 placeholder="Your Name..." 
                 value={name || ""} 
                 onChange={handleInputChange}
                 />
                
                <label htmlFor="emai"><b>Email</b></label>
                <input
                 type="email" 
                 id="email" 
                 name="email" 
                 placeholder="Your Email..." 
                 value={email || ""} 
                 onChange={handleInputChange}
                 />

                <label htmlFor="contact"><b>Contact</b></label>
                <input
                 type="number"
                 id="contact" 
                 name="contact" 
                 placeholder="Your Contact..." 
                 value={contact || ""} 
                 onChange={handleInputChange}
                 />                
                  <lable htmlFor="maritalstatus"><b>Marital Status</b></lable>
                  <br></br>
                  <input
                  type="radio"
                  id="maritalstatus"
                  name="maritalstatus"
                  value={"married"}
                  onChange={handleInputChange}
                  />Married
                    
                <input
                  type="radio"
                  id="maritalstatus"
                  name="maritalstatus"
                  value={"Unmarried"}
                  onChange={handleInputChange}
                  />Unmarried
                  <br></br>

                  <label htmlFor="gcmlevel"><b>Select GCM LEVEL</b></label>
                  <select
                  id="gcmlevel"
                  name="gcmlevel"
                  onChange={handleInputChange}
                  >
                    <option    
                  value={"GCM-1"}
                  >GCM-1
                  </option>
                  
                  <option
                  value={"GCM-2"}
                  >GCM-2
                  </option>
                  <option
                  value={"GCM-3"}
                  >GCM-3
                  </option>
                  <option
                  value={"GCM-4"}
                  >GCM-4
                  </option>
                  <option
                  value={"GCM-5"}
                  >GCM-5
                  </option>
                  <option
                  value={"GCM-6"}
                  >GCM-6
                  </option>
                  </select>
                  <label htmlFor="office"><b>Choose Your office Locations</b></label>
                  <br></br>
                  Pune:<input
                  type="checkbox"
                  id="office1"
                  name="office1"
                  value={"Pune"}
                  onChange={handleInputChange}
                  />
                  &nbsp;&nbsp; Mumbai:<input
                  type="checkbox"
                  id="office2"
                  name="office2"
                  value={"Mumbai"}
                  onChange={handleInputChange}
                  />
                  &nbsp;&nbsp; Chennai:<input
                  type="checkbox"
                  id="office3"
                  name="office3"
                  value={"Chennai"}
                  onChange={handleInputChange}
                  />
                  <br></br>
                  <label htmlFor="dateofjoining"><b>please Select date of Joining</b></label>
                  <br></br>
                  <input
                  type="date"
                  id="dateofjoining"
                  name="dateofjoining"
                  value={dateofjoining || ""}
                  onChange={handleInputChange}
                  />
                  <br></br>
                  <label htmlFor="salary"><b>Select Your exact salary</b></label>
                  <br></br>
                  <div>
                  <input
                  type="range"
                  min="1"
                  max="2000000"
                  id="salary"
                  name="salary"
                  value={salary || "0"}
                  onChange={handleInputChange}
                  oninput="rangeValue.innerText = this.value"
                  /><p id="rangeValue">100</p>
                  </div>
                  <br></br>
                  <div>
                   <b>Are you on bench?</b>
                   <br></br>
                    <input
                    class="checkbox"
                    type="checkbox"
                    id="benchstatus"
                    name="benchstatus"
                    value={benchstatus}
                    //onChange = {()=>setState(!benchstatus)}
                    onChange = {()=>setState({ ...state, benchstatus:!benchstatus  })}
                    />
                     <label htmlFor="benchstatus" className="toggle">
                    <p>NO   Yes</p>
                    </label>
                  </div>
                <input type="submit" value={id ? "update" : "save"}  />
            </form>
            <Link to={"/"} className="link">cancel</Link>
        </div>
    )
}
export default AddEdit
