import React, {useEffect , useState} from 'react';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';
import fireDb from '../firebase';
import './Home.css';


const Home = () => {

  const [data , setData] = useState({});


  useEffect(() => {
    fireDb.child("conacts").on("value" , (snapshot)=>{
      if(snapshot.val() !== null) {
        setData( { ...snapshot.val() });
      }else{
        setData({});
      }
    })
    
    return () => {
      setData({});
    }
  }, []);

const onDelete= (id)=>{
  if(window.confirm('Are you sure you want delete this user?')){
    fireDb.child(`conacts/${id}`).remove((err)=> {
      if(err){
        toast.error(err)
      }else{
        toast.success("user deleted successfully");
      }
    })
  }
}
  return (
      <div className="">
         <table>
         <tr>
         <td>id</td>
         <td>name</td>
         <td>Contact</td>
         <td>email</td>
         <td>Marital Status</td>
         <td>GCM LEVEL</td>
         <td>Office - 1</td>
         <td>Office - 2</td>
         <td>Office - 3</td>
         <td>Date of Joining</td>
         <td>Annual Salary</td>
         <td>Bench Status</td>
         <td>Action</td>
         </tr>
       {Object.keys(data).map((id,index) =>{
         return(
          <tr key={id}> 
          <th> {index + 1}</th>
            <td>{data[id].name}</td>
            <td>{data[id].contact}</td>
            <td>{data[id].email}</td>
            <td>{data[id].maritalstatus}</td>
            <td>{data[id].gcmlevel}</td>
            <td>{data[id].office1}</td>
            <td>{data[id].office2}</td>
            <td>{data[id].office3}</td>
            <td>{data[id].dateofjoining}</td>
            <td>{data[id].salary}</td>
            <td>{data[id].benchstatus?"No":"Yes"}</td>
            <td>
              <Link to={`/update/${id}`}>
                <button type="button">Edit</button>
              </Link>
                <button onClick={ ()=> onDelete(id)}>Delete</button>
             
              <Link to={`/view/${id}`}>
                <button>view</button>
              </Link>


            </td>


            </tr>
         )
       })}
         
       </table>



      </div>
      
  );
}

export default Home;
